import { Fragment, useLayoutEffect } from "react";
import { useDispatch, useSelector } from "react-redux"
import { cartFooterText, cheapText, favItemText, noItemText, proceedToCheckoutText } from "../../constants";
import { addQuantity, removeCartItem, removeQuantity, toggleModal } from "../../redux/actions";
import { displayCartItems } from "../../utils/Validation"
import './Cart.scss'
import Modal from "./CartModal";

const Cart = () => {
    const cartData = useSelector(state => state.TestReducer.cartData)
    const modalFlag = useSelector(state => state.TestReducer.modalFlag)
    const dispatch = useDispatch();

    const addHandler = (e, item) => {
        dispatch(addQuantity(item))
    }

    const removeHandler = (e, item) => {
        item.quantity > 1 ? dispatch(removeQuantity(item)) : dispatch(removeCartItem(item.id))
    }

    const RenderCartList = () => {
        return <Fragment>{cartData.map(item => {
            return <div key={item.id} className="cart-detail-container">
                <img src={item.imageURL} alt={item.name}></img>
                <div className="cart-details-section">
                    <h4 style={{ marginTop: 0, marginBottom: 5 }}>{item.name}</h4>
                    <div className="cart-details-disp-flex">
                        <QuantityBar item={item} addHandler={addHandler} removeHandler={removeHandler} />
                        <p>Rs. {item.quantity * item.price}</p>
                    </div>
                </div>
            </div>
        })}
            <div className="cheap-image-container"><img src="/static/images/lowest-price.png" alt="lowest-price-text" />{cheapText}</div>
        </Fragment>
    }

    const FooterContent = () => {
        return <Fragment><p className="align-center">{cartFooterText}</p>
            <button className="each-category-button-style modal-footer-button-style" disabled={cartData.length === 0 }>
                <div>{proceedToCheckoutText}</div>
                <div>{cartData.map((ele) => ele.price * ele.quantity).reduce((a, b) => a + b, 0).toFixed(2)}</div>
            </button>
        </Fragment>
    }

    return <>
        <Modal
            title={`My Cart (${displayCartItems(cartData)})`}
            show={modalFlag}
            onClose={() => dispatch(toggleModal(false))}
            footerContent={<FooterContent />}
        >
            <>{cartData.length > 0 ?
                <RenderCartList /> :
                <div className="no-item-style">
                    <strong>{noItemText}</strong>
                    <p>{favItemText}</p>
                </div>}
            </>
        </Modal>
    </>
}

const QuantityBar = ({ item, addHandler, removeHandler }) => {
    return <div className="quantity">
        <button className="each-category-button-style circle-button" id="plus" type="button" name="button" onClick={(e) => addHandler(e, item)}>
            +
        </button>
        <input type="text" name="name" value={item.quantity} disabled />
        <button className="each-category-button-style circle-button" id="plus" type="button" name="button" onClick={(e) => removeHandler(e, item)}>
            -
        </button>
        <p> x {item.price}</p>
    </div>
}



export default Cart;